// {"title":"AngleSharp v0.14.0","date":"2020-03-13 07:13:00","categories":"news release","author":"Florian Rappl","userId":"FlorianRappl","section":"Posts","link":"/blog/2020-03-13-anglesharp-v0140","source":"/home/runner/work/anglesharp.github.io/anglesharp.github.io/docs/blog/2020-03-13-anglesharp-v0140.md"}
import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2020-03-13-anglesharp-v0140.md";
    const html = `
<p>In the past few months it was a bit more quiet in the whole AngleSharp ecosystem. There have been a couple of reasons for this, but most importantly: <strong>AngleSharp has reached maturity and is ready for going into 1.0</strong>.</p>
<p>This is a big news. But why are we then reporting a release of 0.14?</p>
<p>For any software to be really mature the documentation plays a crucial role. We are aware that AngleSharp's documentation is <em>unfortunately</em> not <em>that</em> great. We'll try to improve this before going into 1.0.</p>
<p>One of the challenges with a proper documentation for AngleSharp is that we use no mono repo for AngleSharp. This challenge bites us in several ways. We could, actually change this, but for now we'll keep the different repositories as is. Instead, we'll solve the documentation part via microfrontends.</p>
<p>In the next two weeks our homepage will be changed to use <a href="https://piral.io">Piral</a>. All different modules will be loaded via pilets.</p>
<ul>
<li>One module for each library (containing documentation and more infos)</li>
<li>One module for the <strong>blog posts</strong></li>
<li>One module for the <strong>playground</strong></li>
</ul>
<p>You read right - we will bring in a playground. This playground will be based on <strong>Blazor</strong>.</p>
<p>The advantage is that each repository can independently trigger a documentation update. We'll use a GitHub action for this.</p>
<p>More on this will follow!</p>
<p>PS: Yes, 0.14 is still a thing, but it mostly contains awesome bug fixes and improvements. Nothing really special. Why is it breaking? Because it forces <code>IBrowsingContext</code> to be <code>IDisposable</code> for cleanup purposes. Thus if you have an implementation of the <code>IBrowsingContext</code> interface somewhere that does not implement <code>IDisposable</code> you'll need to change your code there.</p>
`;
